.accordion-title {
  display: flex;
  align-items: center;
  padding-left: 65px;
  position: relative;
  height: 40px;
  cursor: pointer;
}

@media screen and (max-width: 750px) {
  .accordion-title {
    height: 10.7vw;
    padding-left: 17.3vw;
