.accordion-title:before {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #3BB6D5;
  color: #fff;
  content: "Q";
  height: 40px;
  left: 0;
  position: absolute;
  width: 40px;
}

@media screen and (max-width: 750px) {
  .accordion-title:before {
    height: 10.7vw;
    width: 10.7vw;
  }
}

.accordion-title:after {
  content: "";
  position: absolute;
  right: 30px;
  top: 38%;
  transition: all 0.2s ease-in-out;
  display: block;
  width: 8px;
  height: 8px;
  border-top: solid 2px #3BB6D5;
  border-right: solid 2px #3BB6D5;
  transform: rotate(135deg);
}

@media screen and (max-width: 750px) {
  .accordion-title:after {
    border-top-width: .53vw;
    border-right-width: .53vw;
    right: 5.33%;
  }
}

.accordion-title.open:after {
  transform: rotate(-45deg);
  top: 45%;
}

